// -----------------------------------------------------------------------------
// This file contains all main js.
// -----------------------------------------------------------------------------

$(document).ready(function() {
    /********************************/
    /*hide input*/
    /********************************/
    $(".toggle-password").click(function() {
        $(this).toggleClass("fa-eye fa-eye-slash");
        var input = $($(this).attr("toggle"));
        if (input.attr("type") == "password") {
            input.attr("type", "text");
        } else {
            input.attr("type", "password");
        }
    });

    /********************************/
    /*responsive sidebar open close menu*/
    /********************************/
    
    $('.openmenu-wtf').click(function(){
        if($('.fix-header').hasClass('show-sidebar')){
            $(".oc-icn-wtf").hover(
                function () {
                  $('.fix-header').removeClass("show-sidebar hide-sidebar");
                },
                function () {
                  $('.fix-header').addClass("show-sidebar hide-sidebar");
                }
            );
        }
        else{
            $(".oc-icn-wtf").unbind('hover');
        }
    });

    /********************************/
    /*menu scrollbar*/
    /********************************/
    $("#arrw-wtf-left").click(function () {
        var leftPos = $('.dv-scrbamn').scrollLeft();
        $(".dv-scrbamn-hotItems").animate({
            scrollLeft: leftPos - 200
        }, 800);
    });

    $("#arrw-wtf-right").click(function () {
        var leftPos = $('.dv-scrbamn').scrollLeft();
        $(".dv-scrbamn-hotItems").animate({
            scrollLeft: leftPos + 200
        }, 800);
    });
    
    /********************************/
    /*show & hide arrow menu tab*/
    /********************************/
    var jumlahmenu = 0;
    $('.dv-scrbamn li').each(function(index) {
        jumlahmenu += parseInt($(this).width());
    });
    var widthTab = $('.scroll-menu-tab').width();
    
    if(jumlahmenu > widthTab){
        $('.scroll-menu-tab')
            .mouseover(function(){
                $('.arrow-scrl').css('visibility', 'visible');
            })
            .mouseout(function(){
                $('.arrow-scrl').css('visibility', 'hidden');
            });
    }
    
    /********************************/
    /*Change Avatar Input*/
    /********************************/
    function readURL(input) {
        if (input.files && input.files[0]) {
            var reader = new FileReader();
            reader.onload = function (e) {
                $('#imagePreview').css('background-image', "url('" + e.target.result + "')");
                $('#imagePreview').hide();
                $('#imagePreview').fadeIn(650);
            }
            reader.readAsDataURL(input.files[0]);
        }
    }
    $("#image").change(function () {
        readURL(this);
    });


    /********************************/
    /*Filter*/
    /********************************/
    $(document).on('click','#filterbtnwtf',function(){
        $(this).addClass("active");
        $('#filterwtf').removeClass("d-none");
        $('#filterwtf').addClass('animate__fadeInRight');
        $('#filterwtf').removeClass('animate__fadeInLeft');
        $('#FilterMask').addClass('d-block');
        $('body').addClass('overflow-hidden');
    
        var mxHght = $(window).height();
        var hgtTopFilter = $('#top-filter').height();
        var hgtBottomFilter = $('#bottom-filter').height();
        var totalHgtFilter = mxHght - (hgtTopFilter + hgtBottomFilter);
        $('#middle-filter').css({
            'max-height' : totalHgtFilter,
            'top' : hgtTopFilter,
            'bottom' : hgtBottomFilter
        });
    });

    $(document).on('click','.btn-wtf.closefilter',function(){
        $(this).removeClass("active");
        $('#filterwtf').addClass("d-none");
        $('#FilterMask').removeClass('d-block');
        $('#filterwtf').addClass('animate__fadeInLeft');
        $('#filterwtf').removeClass('animate__fadeInRight');
        $('body').removeClass('overflow-hidden');
    });
});